@import url('https://fonts.googleapis.com/css2?family=Macondo&family=Nunito:wght@200&display=swap');
*{
  
  padding: 0;
  box-sizing: border-box;
  font-family: 'Macondo', cursive;
  letter-spacing: 2px;
  

}

body{
  background-image: linear-gradient(#d7816a,#bd4f6c);
  
  
}
.App{

 text-align: center;
 justify-content: center;
 padding: 50px;
 margin-left: 100px;
 
 
 
}
ul{
  list-style: none;
  border: none;
  
}
li{
  border: 1px solid coral;
 
  margin: 2px;
 
}
.cont{
  
  display: flex;
  flex-direction: row;
  font-size: 18px;
  zoom:90%

}
.mform {
  background-color: darksalmon;
  justify-content: center;
  align-items: center;
  border: 3px solid black;
  padding: 10px;
  
  
}
.mform h1{
  border: 3px dotted black;
  background-color: tan;
}

.mform label{
  font-weight: bold;
  text-transform: capitalize;
  color:black;
  
}
.mform .gender{
  border:3px solid black;
  display: inline-block;
  border-radius:20%;
  background-color: burlywood;
}


.mform .skills{
  border:3px solid black;
  border-radius: 60px;
  display: inline-block;
  background-color: darkkhaki;
}
.mform label, input{
  padding: 10px;
  margin: 5px;
}
input[type=text],input[type=email]{
  border: 3px solid black;
  border-radius: 10%;
  font-weight: bold;
  width:300px;
  text-align: center;
  background-color: lightcoral;

}
input[type=reset],input[type=submit]{
  border: 3px solid black;
  width: 100px;
  background-color: saddlebrown;
  font-weight: bold;
  letter-spacing: 5px;
  font-size: 16px;
 
}
input[type=reset]:hover{
  opacity: 0.9;
  background-color: sandybrown;

}
input[type=submit]:hover{
  opacity: 0.8;
  background-color: sandybrown;

}

.dcont{

  margin-left: 50px;
  border: 3px solid black;
  padding: 10px;
  background-color: tomato;
}
.dcont h1{
  background-color: tan;
  border: 3px double black;
  padding: 1px;
}
.sdata{
 
  display: flex;
  flex-direction: row-reverse;
  margin-bottom: 10px;
  border:3px solid green;
  padding: 3px;
}

.dpdata{
  display: inline-flex;
  flex-direction: column;
  background-color: indianred;
  border: 3px solid pink;
  padding: 5px;
}
.udata{
  display: flex;
  flex-direction: row;
  padding:10px;
  background-color: coral;
  border: 2px solid maroon;
  margin-bottom: 5px;
  text-align: center;
  justify-content: center;
  letter-spacing: 2px;
 
}
.udata label{

  padding: 2px;
  font-weight: bold;
  text-decoration: underline;
}
.udata p{
  margin: 5px;
  font-style: italic;
}
.uimg{
 background-color: peru;
  margin-left: 50px;
  border: 3px solid  pink;
}
.uimg h1{
  border-bottom: 3px dashed lightcoral;
  padding: 2px;

}
.uimg img{
  width:300px;
  height: 300px;
  padding: 10px;
}




